import firebase from 'firebase';
import app from './firebase';
import axios from 'axios';

export const listH5p = async () => {
  try {
    let querySnapshot = await app.firestore().collection('h5p').get();
    const h5p = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      };
    });
    return h5p;
  } catch (err) {
    console.log(err);
  }
};

export const listH5pByUser = async (userid) => {
  try {
    console.log("search h5p:",userid)
    let querySnapshot = await app.firestore().collection('h5p').where("userid","==",userid).get();
    const h5p = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      };
    });
    console.log(h5p)
    return h5p;
  } catch (err) {
    console.log(err);
  }
};


export const deleteH5ps = (ids,idToken) => {
  try {
    ids.map((id) => {
      //https://h5p.dschool.vn/h5p/delete/95679186
     const url = "https://h5p.dschool.vn/h5p/delete/" + id + "?idToken="+idToken;
    axios.get(url).then(resp => {

        console.log(resp.data);
        app.firestore().collection('h5ps').doc(id).delete();
    });
      
    })
  } catch (err) {
    console.log(err);
  }
};