import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  Checkbox,
  CardContent,
  Grid,
  TextField,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  makeStyles,
  useTheme,
  Input,
  Chip,
  Typography, RadioGroup, FormControlLabel, Radio
} from '@material-ui/core';
import PerfectScrollbar from 'react-perfect-scrollbar';
import wait from 'src/utils/wait';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import {addQuiztest} from '../../../api/quiz';
import { listH5p } from '../../../api/h5p';
import useAuth from 'src/hooks/useAuth';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { listAllTags } from '../../../api/categories';
import { useHistory } from 'react-router';


const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  },
  noLabel: {
    marginTop: theme.spacing(3),
  },
}));

const sortOptions = [
  {
    value: 'category|asc',
    label: 'Category (tăng)'
  },
  {
    value: 'category|desc',
    label: 'Category (giảm)'
  }
];

const applyFilters = (courses, query, filters) => {
  return courses.filter((course) => {
    let matches = true;

    if (query) {
      const properties = ['email', 'name'];
      let containsQuery = false;

      properties.forEach((property) => {
        if (course[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && course[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (courses, page, limit) => {
  return courses.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};
const applySort = (courses, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = courses.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const QuizTestCreateForm = ({ className, ...rest }) => {

  const { user} = useAuth();
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();

  const [quiz, setQuiz] = useState([]);
  const getCourses = useCallback(async () => {
    try {
      const QuizH5p = await listH5p();
      if (isMountedRef.current) {
        setQuiz(QuizH5p);
      }

    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);


  useEffect(() => {
    getCourses();
  }, [getCourses]);
  const { enqueueSnackbar } = useSnackbar();

  const [selectedH5p, setSelectedH5p] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');

  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleSelectAllH5p = (event) => {
    setSelectedH5p(event.target.checked
      ? quiz.map((course) => course.id)
      : []);
  };

  const handleSelectOneH5p = (event, courseId) => {
    if (!selectedH5p.includes(courseId)) {
      setSelectedH5p((prevSelected) => [...prevSelected, courseId]);
    } else {
      setSelectedH5p((prevSelected) => prevSelected.filter((id) => id !== courseId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const filteredCourses = applyFilters(quiz, query, filters);
  const sortedClasses = applySort(filteredCourses, sort);
  const paginatedH5p = applyPagination(sortedClasses, page, limit);
  const selectedSomeH5p = selectedH5p.length > 0 && selectedH5p.length < quiz.length;
  const selectedAllH5p = selectedH5p.length === quiz.length;

  const [typeQuiz, setTypeQuiz] = React.useState('');
  const [open, setOpen] = React.useState(false);

  const audienceChange = (event) => {
    setTypeQuiz(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };

  const [tags, setTags] = useState([]);

  const getTags = useCallback(async () => {
    try {
      const tags = await listAllTags();
      if (isMountedRef.current) {
        setTags(tags);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getTags();
  }, [getTags]);

  function getStyles(tag, tagName, theme) {
    return {
      fontWeight:
        tagName.indexOf(tag) === -1
          ? theme.typography.fontWeightRegular
          : theme.typography.fontWeightMedium,
    };
  }
  const theme = useTheme();
  const [tagName, setTagName] = useState([]);
  const handleChangeTags = (event) => {
    setTagName(event.target.value);
  };
  const history = useHistory();

  return (
    <Formik initialValues={{
        name: '',
        submit: null }}
      validationSchema={Yup.object().shape({
        name: Yup.string().max(75).required('Bắt buộc nhập tên'),
      })}

      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          console.log(tagName);
          if(tagName.length > 0 && selectedH5p.length > 0){
            addQuiztest(values,tagName,typeQuiz,selectedH5p);
            // NOTE: Make API request
            await wait(500);
            resetForm();
            setStatus({ success: true });
            setSubmitting(false);
            enqueueSnackbar('Tạo quiz thành công', {
              variant: 'success',
              action: <Button>See all</Button>
            });
            await wait(1000);
            history.push('/hbon/management/sendnotify/list');
          }else{
            enqueueSnackbar('Tags và list câu hỏi không được để trống', {
              variant: 'error',
              action: <Button>See all</Button>
            });
          }
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          setFieldValue,
          setFieldTouched,
          touched,
          values
        }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit}{...rest}>
          <Card>
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField error={Boolean(touched.name && errors.name)} fullWidth helperText={touched.name && errors.name} label="Tên" name="name" onBlur={handleBlur} onChange={handleChange} required value={values.name} variant="outlined"/>
                </Grid>

                <Grid item md={6} xs={12}>
                  <FormControl className={classes.formControl}  fullWidth>
                    <InputLabel id="demo-controlled-open-select-label">Loại quiz</InputLabel>
                    <Select
                      labelId="demo-controlled-open-select-label"
                      id="demo-controlled-open-select"
                      open={open}
                      onClose={handleClose}
                      onOpen={handleOpen}
                      value={typeQuiz}
                      onChange={audienceChange}
                    >
                      <MenuItem value="">
                        <em>None</em>
                      </MenuItem>
                      <MenuItem value={'h5p'}>H5P</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>

                <Grid item md={6} xs={12}>
                  <Typography gutterBottom variant="h6" color="textPrimary">
                    Tags
                  </Typography>
                  <Typography gutterBottom variant="body2" color="textSecondary">
                    Chọn đối tượng làm quiz
                  </Typography>
                  <FormControl className={classes.formControl}>
                    <Select
                      name="audience"
                      labelId="demo-mutiple-chip-label"
                      id="demo-mutiple-chip"
                      multiple
                      value={tagName}
                      onChange={handleChangeTags}
                      input={<Input id="select-multiple-chip" />}
                      renderValue={(selected) => (
                        <div className={classes.chips}>
                          {selected.map((value) => (
                            <Chip key={value} label={value} name="tags" className={classes.chip} />
                          ))}
                        </div>
                      )}
                      MenuProps={MenuProps}
                    >
                      {tags.map((tag) => (
                        <MenuItem key={tag} value={tag} style={getStyles(tag, tagName, theme)}>
                          {tag}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>

                <Grid item md={6} xs={12}>
                </Grid>

              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  Gửi notification
                </Button>
              </Box>
            </CardContent>
            <PerfectScrollbar>
              <Box minWidth={700}>
                <Table>
                  {typeQuiz === 'h5p' &&(
                  <TableHead>
                    <TableRow>
                      <TableCell padding="checkbox">
                        <Checkbox checked={selectedAllH5p} indeterminate={selectedSomeH5p} onChange={handleSelectAllH5p}/>
                      </TableCell>
                      <TableCell>
                        ID
                      </TableCell>
                      <TableCell>
                        UserId
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  )}
                  <TableBody>
                    {typeQuiz === 'h5p' && (paginatedH5p.map((h5p) => {
                      const isH5pSelected = selectedH5p.includes(h5p.id);
                      return (
                        <TableRow hover key={h5p.id} selected={isH5pSelected}>
                          <TableCell padding="checkbox">
                            <Checkbox checked={isH5pSelected} onChange={(event) => handleSelectOneH5p(event, h5p.id)} value={isH5pSelected}/>
                          </TableCell>
                          <TableCell>
                            {h5p.id}
                          </TableCell>
                          <TableCell>
                            {h5p.userid}
                          </TableCell>
                        </TableRow>
                      );
                    }))}
                  </TableBody>
                </Table>
              </Box>
            </PerfectScrollbar>
            {typeQuiz === 'h5p' &&(
            <TablePagination
              component="div"
              count={filteredCourses.length}
              onChangePage={handlePageChange}
              onChangeRowsPerPage={handleLimitChange}
              page={page}
              rowsPerPage={limit}
              rowsPerPageOptions={[5, 10, 25]}
            />)}
          </Card>
        </form>
      )}
    </Formik>
  );
};

QuizTestCreateForm.propTypes = {
  className: PropTypes.string
};

export default QuizTestCreateForm;
